import moment from 'moment';
import { db, auth } from '../firebase';

export const getUser = ({ email, password }) => {
    return new Promise((resolve, reject) => {
        auth.signInWithEmailAndPassword(email, password)
            .then((res) => {
                resolve();
            })
            .catch((err) => {
                reject(err);
            });
    });
};

export const signup = ({ email, password }) => {
    return new Promise((resolve, reject) => {
        auth.createUserWithEmailAndPassword(email, password)
            .then((res) => {
                resolve();
            })
            .catch((err) => {
                reject(err);
            });
    });
};

export const getPatients = () => {
    return new Promise((resolve, reject) => {
        let startOfMonth = moment().clone().startOf('month').toISOString();
        db.collection('patients')
            .where('createdDate', '>=', startOfMonth)
            .orderBy('createdDate', 'asc')
            .get()
            .then((res) => {
                let patientsList = res.docs.map((doc) => {
                    return { ...doc.data(), docId: doc.id };
                });
                resolve(patientsList);
            })
            .catch((error) => {
                console.log(error);
                reject('Oops!! Something went wrong. Please try again.');
            });
    });
};

export const updatePatients = (patientDetails) => {
    let { docId, ...patientData } = patientDetails;
    return new Promise((resolve, reject) => {
        db.collection('patients')
            .doc(docId)
            .update(patientData)
            .then((res) => {
                console.log(res);
                resolve(res);
            })
            .catch((error) =>
                reject('Oops!! Something went wrong. Please try again.')
            );
    });
};

export const addPatients = (patientDetails) => {
    let { docId, ...patientData } = patientDetails;
    return new Promise((resolve, reject) => {
        db.collection('patients')
            .add(patientData)
            .then((res) => {
                resolve(res.id);
            })
            .catch((error) =>
                reject('Oops!! Something went wrong. Please try again.')
            );
    });
};

export const deletePatients = (patientDetails) => {
    let { docId, ...patientData } = patientDetails;
    return new Promise((resolve, reject) => {
        db.collection('patients')
            .doc(docId)
            .delete()
            .then((res) => {
                resolve(res);
            })
            .catch((error) =>
                reject('Oops!! Something went wrong. Please try again.')
            );
    });
};
